.landing {
    padding: 3rem;
    position: absolute;
    width: 100%;
    height: 100%;
    //grid-column: full-start / full-end;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 1.3;
    @include respond(phone) {
        //justify-content: unset;   
        padding: 0;
    }
    &-step0 {
        transform: translateY(50%);
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        &-less {
            transform: translateY(35rem);
        }
        &-more {
            transform: translateY(70%);
        }
    }

    
    &-nojustify {
        justify-content: flex-start;
        @include respond(phone) { 
            //align-items: flex-start;
            
        }
    }
    

    &__grid {
        position: absolute;
        width: 100%;
        height: 100%;
        margin: 0 auto;
        display: grid;
        justify-content: center;
        align-content: center;
        justify-items: center;
        line-height: 1.3;
     
        @include respond(tab-port) { 
            padding: 3rem;
        }
        @include respond(phone) { 
            align-content: unset;
        }
    }
    &__userbody {
        display: flex;
        margin: 3rem;
        margin-top: 4rem;
        flex-direction: row;
        align-items: flex-start;
        //right left in userbody
        @include respond(tab-port) {
            margin: 3rem;
            margin-top: 2rem;   
        }
        @include respond(tab-port-small) {
            margin: 4rem;
            margin-top: 2rem;   
        }
        @include respond(phone) { 
            margin: 4rem 0;  
        }
        @include respond(phone-small) { 
            margin: 2rem 0;
        };
        
        &-right {
            margin-left: 8rem;
            display: flex;
            flex-direction: column;
            align-items: center;
            flex: 1;
            @include respond(phone) {
                margin-left: 1rem;
                
            }
        }
        &-left {
            display: grid;
            grid-row-gap: 5rem;
            justify-items: flex-start;
            flex: 1;
            @include respond(tab-port-small) { 
                grid-row-gap: 4rem;
            }
            @include respond(phone) {
                grid-row-gap: 2rem; 
            }
            
        }
        //numbers in body svg
        &-length {
            @include respond(tab-land-large) {
                display: none;

            }
            @include respond(phone){
                display: block;
            }
            &-avatar {
                width: 20rem;
                @include respond(phone) {
                    width: 17.5rem;
                }
     
                
                
            }
            top: 14%;
            left: 43%;
            position: absolute;
            display: block;
            width: 4rem; // maybe change to rem 
            text-align: center;
            font-size: 1.2rem;
            font-family: $font-secondary;
            color: #663796;
            font-weight: 700;
            @include respond(phone) {
                top: 5.8rem;
                left: 43.5%;
            }
            &-2 {
                top: 23%;
                @include respond(phone){
                    top:9.1rem;
                }
            }
            &-3 {
                top: 34.5%;
                @include respond(phone){
                    top: 13.7rem;
                }

            }
            &-4 {
              
  
     
                top: 34%;
                left: 76%;
       
                backface-visibility: hidden;
                -webkit-font-smoothing: subpixel-antialiased;
                @include respond(tab-land-large) {
                    display: none;
                }
                @include respond(phone){
                    display: block;
                }
                @include respond(phone) {
                    top: 11rem;
                    left: 83%;
                  
                }
       
               


            }
            &-5 {
                top: 70%;
                left: 10%;
                @include respond(phone){
                    top: 69%;
                    left: 0%;
                }

            }
            &-6 {
                top: 42.3%;
                @include respond(phone) {
                    top: 41.5%;
                }
                

            }

        }
    }
    &__avatar {
        position: relative;
        &-svg {
            width: 20rem;
            height: 40rem;
            @include respond(phone) {
                height: 35rem;
                
            }
        }
    }
    
    //TEXT
    &__text {
        text-align: center;
        font-size: 3.5rem;
        @include respond(tab-port-small) { 
            font-size: 3rem;
            line-height: 1.6;
        }
        @include respond(phone) { 
            line-height: 1.3;
            font-size: 2rem;
        }
        
        //Landing
        &-1 {
            margin-bottom: 3rem;
            margin-top: 0rem;
        }
        //Step1
        &-2 {
            //margin-top: 3.5rem;
            //margin-bottom: 4rem;
            &--top {
               
              
                @include respond(tab-port-small) { 
                    margin-top: 2rem;
                    padding: 0 1.5rem;
                }
            }
            &--bottom {
                margin-bottom: 4rem;
                @include respond(tab-port-small) { 
                    margin-bottom: 2rem;
                    padding: 0 1.5rem;
                }  
            }
        }
        //GetCompanySize
        &-3 {
            font-size: 3.2rem; 
            font-family: $font-secondary;
            margin-bottom: 1.5rem;
            @include respond(phone) { 
                font-size: 2.6rem;
         
            }
            
        }
        // inside companybox
        &-4 {
            
            font-size: 2.7rem;
            font-family: $font-secondary;
            @media only screen and (max-width: 28.6875em) {
                font-size: 2.4rem;
            }
        }
        &-5 {
            margin-top: 3rem;
            font-size: 3.2rem; 
            font-family: $font-secondary;
            @include respond(phone) { 
                font-size: 2.6rem;
         
            }
            
        }
        
        &-large {
            //margin: 2rem;
            margin-bottom: 2.5rem;
            font-size: 3.3rem;
            font-family: $font-display;
            @include respond(tab-port) { 
                font-size: 3rem;
            }
            @include respond(tab-port-small) { 
                font-size: 2.7rem;
            }
            @include respond(phone) {
                font-size: 2rem;
                text-align: center; 
            }
            @include respond(phone-small) { 
                margin: 0;
                font-size: 1.8rem
            };
            
            
        }
        &-medium-large {
            font-family: $font-secondary;
            font-size: 3.5rem;
            //margin-top: 3rem;
            margin-bottom: 0.5rem;
            text-align: center;
            @include respond(tab-port) { 
                font-size: 3rem;  
            }
            @include respond(tab-port-small) { 
                margin-top: 2rem;
            }
        }
        &-medium {
            font-size: 2.8rem;
            text-align: center;
            @include respond(phone) { 
                font-size: 1.6rem;
            }
            &-1 {
                margin-bottom: 3rem;
                @include respond(tab-port) { 
                    margin-bottom: 1rem;
                }

                
            }
            &-semibold {
                font-family: $font-secondary;
                
            }
            &-romanbody {
                font-family: $font-display;
                color: $color-grey-light-bright;
                @include respond(tab-port) { 
                    font-size: 2.5rem;
                   
                }
            } 
        }
        &-avatar{
            margin-bottom: 2.5rem;
            font-size: 3.3rem;
            font-family: $font-display;
            @include respond(phone) {
                margin-bottom: 2rem;
                font-size: 2rem;
                font-family: $font-secondary;
            }
            
            
        }
        &--input {
            font-family: $font-display;
            font-size: 2.4rem;
            @include respond(phone) {
                font-size: 1.6rem;
                //devam
            }
        }

        
    }
    //components
    &__input {
        margin-top: 1rem;
        display: grid;
        grid-template-columns: 25rem 12.5rem;
        
        justify-items: flex-start;
        align-items: center;
        @include respond(phone) {
            margin: 0;
            grid-template-columns: 15rem;
            grid-row-gap: 2rem;
        }
        
        &--box {
            display: flex;
            position: relative;
            box-shadow: 0 1rem 5rem rgba(#000, .2);
            border-radius: 25px;
            overflow: hidden;
            height: 115%;
            
            &-value {
                background-color: transparent;
                font-size: 1.7rem;
                color: inherit;
                border-radius: 2px;
                border: none;
                margin-left: 2rem;
                width: 35%;
                display: block;
                transition: all .3s;
                &:focus {
                    outline: none;
                    background-color: transparent;
      
                   
                }
                &:active {
                    outline: none;
                    background-color: transparent;

                   
                }
            }
            &-text {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 5rem;
                margin-left: auto;
                border: 2px solid $color-grey-light;
                border-radius: 25px;
                font-size: 1.6rem;
            }

        }
        
    }
    &__button {
        &-toosmall {
            font-size: 2rem;
            margin-top: 3rem;
        }
        &-smaller {
            font-family: $font-secondary;
            font-size: 2.7rem;
            text-transform: capitalize;
        }
        &-small {
            //padding: 2rem 8rem;
            font-family: $font-secondary;
            font-size: 3.5rem;
            text-transform: capitalize;
            @include respond(tab-port-small) { 
                font-size: 2.7rem;
            }
            @include respond(phone) { 
                font-size: 2rem;
            }
        }
        &-wide {
            border: 1px transparent solid;
            padding: 4rem 3rem;
            margin-bottom: 2rem;
            width: 70rem;
            text-align: center;
            font-family: $font-secondary;
            text-transform: initial;
            font-size: 2.4rem;
            @include respond(phone) { 
                width: 90vw;
                font-size: 1.6rem;
                padding: 3rem;
            }
            &-checkbox {
                display: none;
                &:checked+label {
                    transform: scale(0.97);
                    box-shadow: 0 1rem 5rem rgba(#000, .6);
                    border: 1px solid $color-black;
                  }
                  
    
            }
        }
        
    }

    // GET SIZE WITH COMPANIES

    
    &__companylogos {
 
        width: 100%;
        margin: 5rem;
        margin-bottom: 7rem;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(17.5rem, 1fr));
        grid-template-rows: repeat(auto-fit, minmax(17.5rem, 1fr));
        grid-gap: 1.5rem;
        grid-row-gap: 3rem;
        &-tworows { //there s some bug which does not center the content when we are using csstransition in both step forms(getcompaniessize, getuserbody), there would not be any problem if we had used router but this works.
            grid-template-columns: repeat(5, minmax(17.5rem, 1fr));

        }
        @include respond(tab-port) { 
            grid-gap: 2rem;
            grid-template-columns: repeat(5, minmax(12rem, 15rem));
            grid-template-rows: repeat(2, 15rem);
            justify-content: center;
            margin-bottom: 5rem;
        }
        @include respond(tab-port-small) { 
            grid-gap: 2rem;
            grid-template-columns: repeat(4, minmax(12rem, 15rem));
            grid-template-rows: repeat(3, 15rem);
            justify-content: center;
            margin: 3rem;
        }
        @include respond(phone) { 
            grid-gap: 5rem;
            grid-template-columns: repeat(auto-fit, minmax(10rem, 14rem));
            grid-template-rows: repeat(auto-fit, minmax(10rem, 14rem));
            justify-content: center;
            margin: 2rem 0;
        }
       
    }
    &__companylogo {
        height: 17.5rem;
        width: 17.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        //background:url(./hmlogo.png) no-repeat center;
        //background-size: contain;
        border-radius: 250px;
        transition: all .2s;
        cursor: pointer;
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        @include respond(tab-port) { 
            height: 15rem;
            width: 15rem;
           
        }
        @include respond(phone) { 
            height: 15rem;
            width: 15rem;
        }
        &-checkbox {
            
            display: none;
            &:checked+label {
                
                transform: scale(0.90);
                //box-shadow: 0 1rem 5rem rgba(#000, .8);
                border: rgb(44, 44, 44) 2px solid;
              }
              

        }
        
        
    }
    &__companyblocks {
        position: relative;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(55rem, 1fr));
        margin-top: 2rem;
        
        @include respond(phone) { 
            //padding: 2rem;
            
        }
        @media only screen and (max-width: 28.6875em) {
            grid-template-columns: repeat(auto-fit, minmax(28rem, 39rem));
        }
        

        & > * {
            &:not(:last-child) {
                margin-bottom: 2rem;
            }
        }
    }

    //GetCompanySize
    &__companyblock {
        display: grid;
        //border: 1px black solid;
        grid-template-columns: 7rem 15rem 1fr;
        transition: all 0.4s;
        grid-column-gap: 3rem;
        @include respond(phone) { 
            grid-column-gap: 2rem;
            grid-template-columns: 0 12.5rem 1fr 0;
        }
        @media only screen and (max-width: 28.6875em) {
            grid-template-rows: 1fr 1fr;
            grid-template-columns: 1fr;
        }
        &--size {
            display: flex;
        }

        
       
        &--remove {
            grid-row: 1 / 2;
            grid-column: 1 / 2;
            display: flex;
            background-color: #5036a6;
            border-radius: 250px;
            width: 5rem;
            height: 5rem;
            color: #fff;
            align-self: center;
            justify-self: center;
            justify-content: center;
            align-items: center;
            transition: all .2s;
            &:hover {
                transform: translateY(-2px) scale(1.10);
                box-shadow: 0 1rem 5rem rgba(#5036a6, .6);
            }
            @include respond(phone) { 
                grid-column: 2 / 3;
                grid-row: 1 / 2;
                align-self: end;
                width: 4rem;
                height: 4rem;
            }
            @media only screen and (max-width: 28.6875em) {
                grid-column: 1 / -1;
                margin-right: 20rem;
                align-self: center;
            }
                
            
            &-line {
                width: 3rem;
                height: 3px;
                background-color: #fff;
                display: inline-block;
                @include respond(phone) {
                    width: 2.2rem;
                    height: 2.2px;
                 }
            }
            &-text {
                display: none;
                grid-row: 1 / 2;
                grid-column: 1 / 2;
                align-self: center;
                text-align: center;
                transform: translateY(5rem);
                font-size: 1.6rem;
                font-family: $font-display;
                @include respond(phone) {
                    display: none;
                 }
               


            }
      
        }
        
        &-logo {
            grid-column: 2 / span 1;
            grid-row: 1 / span 1;
            width: 15rem;
            height: 15rem;
            @include respond(phone) { 
                height: 12.5rem;
                width: 12.5rem;
            }
            @media only screen and (max-width: 28.6875em) {
                grid-column: 1 / -1;
                justify-self: center;
                align-self: center;
            }
            
        }
        &-accountpage {

            width: 10rem;
            height: 10rem;
            @include respond(phone) { 
                height: 7.5rem;
                width: 7.5rem;
            }
            @media only screen and (max-width: 28.6875em) {
                grid-column: 1 / -1;
                justify-self: center;
                align-self: center;
            }
            
        }
        &-logoaccount {

            width: 15rem;
            height: 15rem;
            @include respond(phone) { 
                height: 12.5rem;
                width: 12.5rem;
            }
            @media only screen and (max-width: 28.6875em) {
                grid-column: 1 / -1;
                justify-self: center;
                align-self: center;
            }
            
        }
        &--selectsize {
            grid-column: 3 / span 1;
            grid-row: 1 / span 1;
            display: flex;
            flex-direction: column;
            @media only screen and (max-width: 28.6875em) {
                grid-row: 2 / span 1;
                grid-column: 1 / -1;
                text-align: center;
                
            }
            &-size {
                margin-top: 1rem;
                display: grid;
                grid-template-columns: repeat(auto-fit, minmax(5rem, 9rem));
                grid-gap: 1.6rem;
                text-transform: uppercase;
                @include respond(phone) { 
                    grid-template-columns: repeat(auto-fit, minmax(9rem, 1fr));
                    grid-gap: 1rem;
                }
                
            }
        }
        
        

        
    }
    
}
