.avatar {
    display: flex;
    flex-direction: column;
   
    
    padding: 7.5rem;
    background: #ab7fd8;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to bottom, #a1c4fd, #ab7fd8);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to bottom, #a1c4fd, #ab7fd8); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    
    
    @include respond(tab-port) {
        padding: 0;
        box-shadow: none;
        margin: 3rem;
        flex-direction: column;
    }
    @include respond(tab-port-small) {
        margin: 3rem;
    }
    @include respond(phone) {
        margin: 1rem;
    }
    &__wrapper {
        height: 100%;
        width: 100%;
        display: flex;

        

    }
    &__inputs {
        display: grid;
        grid-gap: 2rem;
        &--control {
            display: grid;
            grid-template-columns: 1fr 10rem;
            grid-gap: 1rem;
            @include respond(tab-port) {
                grid-template-columns:1fr;
            }
            @include respond(phone) { 
                grid-template-columns: 1fr;
            }
        }
    }
    &__input {
        &--label {
            color: black;
            font-weight: 500;
            font-size: 1.6rem;
            @include respond(big-desktop) {
                font-size: 1.6rem;
            }
        }
        &--input {
            font-size: 1.6rem;
            font-weight: 400;
            line-height: inherit;
            width: 100%;
            height: auto;
            padding: 0.5rem 1rem;
            border: none;
            outline: none;
            border-radius: 1rem;
            color: #fff;
            text-rendering: optimizeLegibility;
            background: linear-gradient(to right, #b26dfa, #7367a0);
           
        }
    }
    &__overlay {
        width: 180px;
        position: absolute;
        @include absCenter;
    }

}
.none {
    display: none;
}

.bg {
    animation:slide 3s ease-in-out infinite alternate;
    background-image: linear-gradient(-60deg, #663796 40%, #a1c4fd 50%);

    bottom:0;
    left:-50%;
    opacity:.5;
    position:fixed;
    right:-50%;
    top:0;
    z-index:-1;
  }
  
  .bg2 {
    animation-direction:alternate-reverse;
    animation-duration:4s;
  }
  
  .bg3 {
    animation-duration:5s;
  }
  
  @keyframes slide {
    0% {
      transform:translateX(-25%);
    }
    100% {
      transform:translateX(25%);
    }
  }

